import React, { useEffect, useState } from "react";
import style from "../PromoteDepartment/PromoteDepartment.module.css";
import JoinFormInput from "../../../../JoiningForm/Student/Form/JoinFormInput";
import QLoader from "../../../../Loader/QLoader";
import { useUpdateMutlipleLeaveConfig } from "../../../../hooks/member_tab/department-api";
import { useTranslation } from "react-i18next";
const LeavesAndTransferConfiguration = ({
  onRefetch,
  leaveConfig,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    c_l: "",
    m_l: "",
    s_l: "",
  });
  const [disabled, setDisabled] = useState(false);
  const [updateMutlipleLeaveConfig] = useUpdateMutlipleLeaveConfig();

  useEffect(() => {
    if (leaveConfig) {
      setState({
        c_l: leaveConfig?.casual_leave ?? "",
        m_l: leaveConfig?.medical_leave ?? "",
        s_l: leaveConfig?.sick_leave ?? "",
      });
    }
  }, [leaveConfig]);
  const onUpdate = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: +e.target.value,
    }));
  };

  const onSave = () => {
    if (instituteId) {
      setDisabled((pre) => !pre);
      updateMutlipleLeaveConfig({
        id: instituteId,
        leaveConfig: state,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <div
        className={style.with_search}
        style={{
          marginTop: "1rem",
        }}
      >
        <h6>{t("leave_configuration")}</h6>
      </div>

      <JoinFormInput
        labelText={t("casual_leave")}
        placeholder={t("casual_leave_placeholder")}
        name={"c_l"}
        value={state?.c_l}
        type={"tel"}
        onChange={onUpdate}
      />
      <JoinFormInput
        labelText={t("medical_leave")}
        placeholder={t("medical_leave_placeholder")}
        name={"m_l"}
        value={state?.m_l}
        type={"tel"}
        onChange={onUpdate}
      />
      <JoinFormInput
        labelText={t("sick_leave")}
        placeholder={t("sick_leave_placeholder")}
        name={"s_l"}
        value={state?.s_l}
        type={"tel"}
        onChange={onUpdate}
      />

      <button
        className={style.select_application_btn}
        style={{
          width: "100%",
          justifyContent: "center",
        }}
        onClick={onSave}
      >
        {t("save")}
      </button>
      {disabled && <QLoader />}
    </>
  );
};

export default LeavesAndTransferConfiguration;
